import pageStatus from '../../components/status/status.vue';
import inputGroup from '../../components/inputGroup/inputGroup.vue';
import interfaces from '../../utils/interfaces.js';

export default {
	data() {
		return {
			phone: '',
			verifyCode: '',
			errors: {},
			btnTitle: '获取验证码',
			disabled: false
		};
	},
	computed: {
		isClick() {
			console.log(this.phone)
			console.log(this.verifyCode)
			if (!this.phone || !this.verifyCode) return true;
			else return false;
		}
	},
	components: {
		pageStatus,
		inputGroup
	},
	methods: {
		handleLogin(e) {
			// this.phone = e.detail.value.phone;
			// this.verifyCode = e.detail.value.verifyCode;
			
			let that = this;
			// 取消错误提醒
			that.errors = {};
			// 发送请求

			that.request({
				url: interfaces.sms_back,
				data: {
					phone: that.phone,
					code: that.verifyCode
				},
				method: 'POST',
				success: res => {
					that.setStor({ userId: res.user._id, nickname: 'Yan', face: 'http://img3.imgtn.bdimg.com/it/u=2141623099,2896788564&fm=26&gp=0.jpg' });
				}
			});
		},
		setVerifyCode(code) {
			console.log(code)
			this.verifyCode = code
		},
		getVerifyCode(phone) {
			let that = this;
			that.phone = phone
			if (this.validatePhone()) {
				this.validateBtn();
				// 发送网络请求

				that.request({
					url: interfaces.sms_send,
					data: {
						tpl_id: '140481',
						key: '795be723dd9e88c3ea98e2b6713ab795',
						phone: that.phone
					},
					method: 'POST',
					success: res => {
						console.log(res);
					}
				});
			}
		},
		validateBtn() {
			let time = 60;
			let timer = setInterval(() => {
				if (time == 0) {
					clearInterval(timer);
					this.btnTitle = '获取验证码';
					this.disabled = false;
				} else {
					// 倒计时
					this.btnTitle = time + '秒后重试';
					this.disabled = true;
					time--;
				}
			}, 1000);
		},
		validatePhone() {
			// 验证手机号码
			if (!this.phone) {
				this.errors = {
					phone: '手机号码不能为空'
				};
				return false;
			} else if (!/^1[345678]\d{9}$/.test(this.phone)) {
				this.errors = {
					phone: '请填写正确的手机号码'
				};
				return false;
			} else {
				this.errors = {};
				return true;
			}
		},
		appOAuthLogin(e) {
			var that = this;
			// 获取用户的登录类型
			var logintype = e.currentTarget.dataset.logintype;

			// 授权登录
			uni.login({
				provider: logintype,
				success(loginRes) {
					// 授权登录成功以后，获取用户的信息
					uni.getUserInfo({
						provider: logintype,
						success(info) {
							// console.log(JSON.stringify(info));
							var userInfo = info.userInfo;
							var face = '';
							var nickname = '';
							var openIdOrUid = '';
							if (logintype == 'weixin') {
								face = userInfo.avatarUrl;
								nickname = userInfo.nickName;
								openIdOrUid = userInfo.openId;
							} else if (logintype == 'qq') {
								openIdOrUid = userInfo.openId;
								nickname = userInfo.nickname;
								face = userInfo.figureurl_qq_2;
							} else if (logintype == 'sinaweibo') {
								openIdOrUid = userInfo.id;
								nickname = userInfo.nickname;
								face = userInfo.avatar_large;
							}
							// 调用开发者后台，执行一键注册或登录
							//that.login(openIdOrUid, nickname, face) //因为后台没有开发第三方登陆注册功能，所以做个假的
							var parData = {
								openIdOrUid,
								nickname,
								face
							};
							that.otherLogin(parData);
						}
					});
				}
			});
		},
		otherLogin(data) {
			let that = this;
			that.request({
				url: interfaces.sms_back,
				data: {
					phone: '15920142495',
					code: '746976'
				},
				method: 'POST',
				success: res => {
					let parmData = Object.assign({ userId: res.user._id }, data);
					that.setStor(parmData);
				}
			});
		},
		login(openIdOrUid, nickname, face) {
			let that = this;
			that.request({
				url: interfaces.thirdLogin,
				data: {
					openIdOrUid: openIdOrUid,
					nickname: nickname,
					face: face
				},
				method: 'POST',
				success: res => {
					var userInfo = result.data;
					// 保存用户信息到全局的缓存中
					that.setStor(data);
				}
			});

			uni.request({
				url: me.serverUrl + '/appUnionLogin/' + logintype,
				data: {
					openIdOrUid: openIdOrUid,
					nickname: nickname,
					face: face
				},
				method: 'POST',
				success(result) {
					if (result.data.status == 200) {
						var userInfo = result.data.data;
						// 保存用户信息到全局的缓存中
						uni.setStorageSync('globalUser', userInfo);
						// 切换页面跳转，使用tab切换的api
						uni.switchTab({
							url: '../me/me'
						});
					}
				}
			});
		},
		setStor(data) {
			uni.setStorage({
				key: 'ele_login',
				data: data,
				success: function() {
					uni.switchTab({
						url: '../tabBar/user/user'
					});
				}
			});
		},
		// 实现在微信小程序端的微信登录
		wxLogin(e) {
			var that = this;
			// 通过微信开发能力，获得微信用户的基本信息
			var userInfo = e.detail.userInfo;

			// 实现微信登录
			uni.login({
				provider: 'weixin',
				success(loginResult) {
					// console.log(loginResult);
					// 获得微信登录的code：授权码
					var code = loginResult.code;
					// 设置登录到哪个对于的微信小程序，大家可以根据自己的后端去实现业务参数

					//因为后台没有开发第三方登陆注册功能，所以做个假的
					var parData = {
						openIdOrUid: 111,
						nickname: userInfo.nickName,
						face: userInfo.avatarUrl
					};

					that.otherLogin(parData);
				}
			});
		}
	}
};
